<template>
  <div class="set-filter">
    <div class="set-list">
      <button 
        class="set-button"
        :class="{ active: !selectedSet }"
        @click="$emit('clear')"
      >
        <span class="material-symbols-rounded">category</span>
        All Sets
      </button>
      <button 
        v-for="set in sets" 
        :key="set.id"
        class="set-button"
        :class="{ active: selectedSet === set.id }"
        @click="$emit('select', set.id)"
      >
        <img 
          v-if="hasLogo(set)" 
          :src="set.images.logo" 
          :alt="set.name"
          class="set-logo"
        />
        <span v-else class="material-symbols-rounded">style</span>
        {{ set.name }}
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SetFilter',
  props: {
    sets: {
      type: Array,
      default: () => []
    },
    selectedSet: {
      type: String,
      default: null
    }
  },
  methods: {
    hasLogo(set) {
      return set.images && set.images.logo;
    }
  }
}
</script>

<style scoped>
.set-filter {
  margin-bottom: 24px;
  background: var(--card-background);
  border-radius: 16px;
  padding: 16px;
  border: 1px solid var(--card-border);
}

.set-list {
  display: flex;
  gap: 12px;
  overflow-x: auto;
  padding: 4px;
  scrollbar-width: none;
  -ms-overflow-style: none;
  scroll-behavior: smooth;
}

.set-list::-webkit-scrollbar {
  display: none;
}

.set-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  border: 1px solid var(--card-border);
  border-radius: 100px;
  background: var(--surface-background);
  color: var(--text-secondary);
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  white-space: nowrap;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.set-button:hover {
  background: var(--background-elevated);
  transform: translateY(-1px);
  box-shadow: var(--shadow-sm);
}

.set-button.active {
  background: var(--accent-primary);
  color: white;
  border-color: transparent;
  box-shadow: var(--shadow-md);
}

.set-logo {
  width: 24px;
  height: 24px;
  object-fit: contain;
}

.material-symbols-rounded {
  font-size: 20px;
}

@media (max-width: 768px) {
  .set-button {
    padding: 10px 16px;
    font-size: 13px;
  }
  
  .set-logo {
    width: 20px;
    height: 20px;
  }
}
</style>
